import { h } from 'preact'
import styled from 'styled-components'

const Approach = styled.section`
    height: 40em;
    background-color: white;
`
const Container = styled.div`
    width: 70%;
    margin: auto;
    padding-top: 20em;
`

const Parent = styled.div`
    display: flex;
`

const FlexChild = styled.div`
    ${({ flex }) => flex && `flex: ${flex};`};
`

export const Heading = styled.h1`
    color: white;
    font-size: 2em;
    text-align: center;
    letter-spacing: 0.15em;
    width: 50%;
    margin: auto;
    font-weight: 500;
    text-align: left;
    line-height: 1.8em;
    font-style: italic;

    ${media.mobile`
      padding-top: 6em; 
      font-size: 1.4em;
      width: 70%;
    `};
`

export default () => (
    <Approach>
        <Container>
            <Parent>
                <FlexChild flex="2 0 0">
                <Heading></Heading>
                </FlexChild>
                <FlexChild flex="1 0 0">2</FlexChild>
            </Parent>
        </Container>
    </Approach>
)



